
.cartcontrol{
   font-size: 0;
}
.cart-decrease{
  display: inline-block;
  padding: 6px;
  transition: all 0.4s linear;
}
.move-enter{
    opacity: 1;
    -webkit-transform: translate3d(48px,0,0);
            transform: translate3d(48px,0,0);
}
.move-enter-active{
    opacity: 0;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
}
.cart-decrease .inner{
  display: inline-block;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0,160,220);
}
.move-leave{
    opacity: 0;
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0)
}
.move-leave-active{
    opacity: 1;
    -webkit-transform: translate3d(24px,0,0);
            transform: translate3d(24px,0,0);
}
.cart-count{
  display: inline-block;
  vertical-align: top;
  width: 12px;
  padding: 6px;
  line-height: 24px;
  text-align: center;
  font-size: 10px;
  color: rgb(147,153,159)
}
.cart-add{
  display: inline-block;
  padding: 6px;
  line-height: 24px;
  font-size: 24px;
  color: rgb(0,160,220)
}



